<template>
    <section>
        <dynamic-form ref="EmploymentForm" lang="en" :buttons="buttons" :schema="FormSchema"
            v-on:OnSubmit="onSubmit"></dynamic-form>
        <modalpreview v-if="showpreviewModal" :PersonalDetailObj="PersonalDetailObj" :CompensationDetailObj="CompensationDetailObj" :EmploymentDetailObj="EmploymentDetailObj" :TemplateId="TemplateId" @close="showpreviewModal = false"></modalpreview>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import modalpreview from "../Candidate/modalpreview.vue"
export default {
    components: { modalpreview },
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
        JobTitle: {
            type: String,
            required: true
        },
        PersonalDetailObj:{
            type: Object,
            required: true
        },
        CompensationDetailObj:{
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            showpreviewModal: false,
            TemplateId: 0,
            CompanyId:null,
            Userid:null,
            DepartmentName:'',
            EmployeeTypeName:'',
            ReportManagerName:'',
            DepartmentData:[],
            EmploymentDetailObj:[],
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: this.$t('EmploymentDetail'),
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('JobTitle'),
                            name: "JobTitle",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Department'),
                            name: "Department",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetEmployeeDDlName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "MultiSelectField",
                            label: this.$t('JobLocation'),
                            name: "JobLocation",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('EmploymentType'),
                            name: "EmploymentType",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "Full Time" }, { value: "2", name: "Contract" }, { value: "3", name: "Part Time" }],
                                onChange: this.GetEmployeeDDlName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ReportingManager'),
                            name: "ReportingManager",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetEmployeeDDlName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('JoiningDate'),
                            name: "JoiningDate",
                            value: "",
                            validationRules: { "required": true },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: this.$t('OfferValidTill'),
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('Date'),
                            name: "Date",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('OfferTemplate'),
                            name: "OfferTemplate",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-info",
                    text: "<i class='fa fa-eye pr-2'></i>Preview",
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-envelope-o pr-2'></i>Email Offer",
                    isVisible: true,
                },
            ],
            ListData:[]
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId= this.GetUserInfo.CompanyId;
        vm.Userid= this.GetUserInfo.ID;
        await vm.ShowDepartment();
        await vm.ShowLocation();
        await vm.ShowManager();
        await vm.ShowTemplate();
        await vm.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            vueObj.ListData=[];
            var url = `applicantId=${vueObj.ApplicantId}&postId=${vueObj.JobId}`;
            DataServices.GetEmployementInformation(url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        (vueObj.FormSchema)[0].Data[0].value = response.data.DATA[0].post_title;
                        (vueObj.FormSchema)[0].Data[1].value = response.data.DATA[0].department_id;
                        (vueObj.FormSchema)[1].Data[1].value = response.data.DATA[0].job_type_id;
                        (vueObj.FormSchema)[2].Data[0].value = response.data.DATA[0].reporting_manager_id;
                        (vueObj.FormSchema)[2].Data[1].value = response.data.DATA[0].joining_date;
                        (vueObj.FormSchema)[3].Data[0].value = response.data.DATA[0].offer_end_date;
                        (vueObj.FormSchema)[3].Data[1].value = response.data.DATA[0].offer_template_id;
                        vueObj.GetEmployeeDDlName('Department','',response.data.DATA[0].department_id);
                        vueObj.GetEmployeeDDlName('EmploymentType','',response.data.DATA[0].job_type_id);
                        vueObj.GetEmployeeDDlName('ReportingManager','',response.data.DATA[0].reporting_manager_id);
                        response.data.LOCATION_DATA.forEach(item => {
                                vueObj.ListData.push({
                                    value: item.value,
                                    label: item.name
                                })
                            })
                            vueObj.FormSchema[1].Data[0].value = vueObj.ListData;
                    }
                }
            );
        },
        ShowDepartment: async function () {
            var vm = this;
            vm.DepartmentData=[];
            var params = `ids=&moduleName=&type=HR_DEPT&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[0].Data[1].config.options = response.data.DATA;
                    vm.DepartmentData= response.data.DATA;
                }
                vm.$refs.EmploymentForm.UpdateKeyValue();
            })
        },
        GetEmployeeDDlName: function(x,y,id){
            var vm=this;
            if(y.name=="Department" || x=="Department"){
            vm.DepartmentName='';
            if(x=="Department"){
                var filterEventName = {};
                filterEventName = vm.DepartmentData.filter(z => z.value == id);
                vm.DepartmentName = filterEventName[0].name;
            }else{
                var filterEventName = {};
                filterEventName = vm.DepartmentData.filter(z => z.value == y.value );
                vm.DepartmentName = filterEventName[0].name;
            }
        }
        if(y.name=="EmploymentType" || x=="EmploymentType"){
            vm.EmployeeTypeName='';
            if(x=="EmploymentType"){
                vm.EmployeeTypeName='';
                if(id=='1'){
                vm.EmployeeTypeName='Full Time'; 
                }else if(id=='2'){
                vm.EmployeeTypeName='Contract'; 
                }else if(id=='3'){
                vm.EmployeeTypeName='Part Time'; 
                }
            }else{
                vm.EmployeeTypeName='';
                if(y.value=='1'){
                vm.EmployeeTypeName='Full Time'; 
                }else if(y.value=='2'){
                vm.EmployeeTypeName='Contract'; 
                }else if(y.value=='3'){
                vm.EmployeeTypeName='Part Time'; 
                }
            }
        }
        if(y.name=="ReportingManager" || x=="ReportingManager"){
            vm.ReportManagerName='';
            if(x=="ReportingManager"){
                var filterEventName = {};
                filterEventName = vm.ReportingMngData.filter(z => z.value == id);
                vm.ReportManagerName = filterEventName[0].name;
            }else{
                var filterEventName = {};
                filterEventName = vm.ReportingMngData.filter(z => z.value == y.value );
                vm.ReportManagerName = filterEventName[0].name;
            }
        }
        },
        ShowLocation: async function () {
            var vm = this;
            var params = `ids=&moduleName=HIRING&type=HR_LOCAT&search=&refId=`
            DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[1].Data[0].config.options = response.data.DATA;
                }
                vm.$refs.EmploymentForm.UpdateKeyValue();
            })
        },
        ShowManager: async function () {
            var vm = this;
            vm.ReportingMngData=[];
            var params = `ids=&moduleName=&type=USERS&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[2].Data[0].config.options = response.data.DATA;
                    vm.ReportingMngData=response.data.DATA;
                }
                vm.$refs.EmploymentForm.UpdateKeyValue();
            })
        },
        ShowTemplate: async function () {
            var vm = this;
            var params = `ids=&moduleName=&type=OFFER_LETTER_TEMPLATE&search=&refId=`
            DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[3].Data[1].config.options = response.data.DATA;
                }
                vm.$refs.EmploymentForm.UpdateKeyValue();
            })
        },
        Close: function () {
            this.$parent.AddTagPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var inputString = val;
            var previewRegex = /<\/i>(.*)/;
            var previewMatch = inputString.match(previewRegex);
            var RequestFrom = previewMatch ? previewMatch[1] : "";
            var jsonobj = {
                    JOBTITLE: formfields.JobTitle,
                    DEPARTMENT: formfields.Department,
                    LOCATION: formfields.JobLocation[0]==undefined?formfields.JobLocation.value:formfields.JobLocation[0].value,
                    EMPLOYETYPE: formfields.EmploymentType,
                    REPORTINGMANAGER: formfields.ReportingManager,
                    JOININGDATE: formfields.JoiningDate,
                    ENDDATE: formfields.Date,
                    OFFERTEMPLATE: formfields.OfferTemplate,
                    COMPANYID: vm.CompanyId,
                    CREATEDBY: vm.Userid,
                    APPLICANTID: vm.ApplicantId,
                    POSTID: vm.JobId,
                    DepartmentName: vm.DepartmentName,
                    EmployeeTypeName: vm.EmployeeTypeName,
                    ReportManagerName: vm.ReportManagerName
            };
            vm.EmploymentDetailObj.push(jsonobj);
            var params = JSON.stringify(jsonobj);
            if (RequestFrom == 'Preview') {
                vm.TemplateId = formfields.OfferTemplate;
                vm.showpreviewModal = true;
            } else if(RequestFrom == 'Email Offer'){
                var EmailData={
                    PersonalDetailObj: vm.PersonalDetailObj,
                    CompensationDetailObj: vm.CompensationDetailObj,
                    EmploymentDetailObj: vm.EmploymentDetailObj
                }
                var Emailparams = JSON.stringify(EmailData);
                vm.TemplateId = formfields.OfferTemplate;
                vm.$router.push({ name: 'HREmailOffer', params: { applicantId: vm.ApplicantId, jobid:vm.JobId, TemplateId: vm.TemplateId, Emailparams:Emailparams} });
            }
            else {
                DataServices.SaveEmployementInformation(params).then((response) => {
                    if (response.data[0].Status == 'Success') {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("EventLocationInfo")]), "success", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("EventLocationInfo")]), "failure", true, vm.$t("Alert"));
                    }
                });
            }
        }
    }
}
</script>