<template>
    <section>
        <dynamic-form ref="PersonalDetailForm" lang="en" :buttons="buttons" :schema="FormSchema"
            v-on:OnSubmit="onSubmit"></dynamic-form>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        PersonalInfoData: {
            type: Object,
            required: true
        },
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CompanyId: null,
            Userid: null,
            CountryName: '',
            PersonalDetailObj: [],
            CountryData: [],
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: this.$t('PersonalInformation'),
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "FirstName",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('MiddleName'),
                            name: "MiddleName",
                            value: "",
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "LastName",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('PersonalEmail'),
                            name: "PersonalEmail",
                            value: "",
                            validationRules: { "required": true },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "PhoneField",
                            label: this.$t('Phone'),
                            name: "Phone",
                            value: "",
                            config: {
                                dependentOn: null,
                                fieldCode: null,
                                mode: "date",
                                options: [],
                                showWhen: null,
                                valueMapping: null,
                            },
                            validationRules: { "required": true },
                            visibility: true,
                            mode: ""
                        },
                        {
                            astype: "PhoneField",
                            label: this.$t('Mobilenumber'),
                            name: "MobileNumber",
                            value: "",
                            config: {
                                dependentOn: null,
                                fieldCode: null,
                                mode: "date",
                                options: [],
                                showWhen: null,
                                valueMapping: null,
                            },
                            validationRules: { "required": true },
                            visibility: true,
                            mode: ""
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('DateOfBirth'),
                            name: "DateOfBirth",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Gender'),
                            name: "Gender",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "Male" }, { value: "2", name: "Female" }, { value: "3", name: "Other" }],
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: this.$t('ContactAddress'),
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Street'),
                            name: "Street",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetCountryName
                            },
                            validationRules: { "required": true },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "ZipCode",
                            value: "",
                            validationRules: { "required": true },
                        },
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Continue"
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        await vm.ShowCountry();
        vm.FetchData();
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&departmentIds=0&skillIds=0&langCode=EN&jobID=${vm.JobId}`;
            await DataServices.GetCandidateDetails(url).then(
                async function (response) {
                    vm.candidateData = [];
                    if (response.data != null && response.data != "" && response.data.data.length > 0) {
                        vm.candidateData = response.data.data;
                        vm.JobTitle = response.data.data[0].POST_TITLE;

                        if (vm.candidateData && vm.candidateData.length > 0) {
                            (vm.FormSchema)[0].Data[0].value = vm.candidateData[0].FIRST_NAME;
                            (vm.FormSchema)[0].Data[1].value = vm.candidateData[0].MIDDLE_NAME;
                            (vm.FormSchema)[1].Data[0].value = vm.candidateData[0].LAST_NAME;
                            (vm.FormSchema)[1].Data[1].value = vm.candidateData[0].email_id;
                            (vm.FormSchema)[2].Data[0].value = vm.candidateData[0].phone_number;
                            (vm.FormSchema)[2].Data[1].value = vm.candidateData[0].phone_number;
                            (vm.FormSchema)[3].Data[0].value = vm.candidateData[0].date_of_birth;
                            (vm.FormSchema)[3].Data[1].value = 1;
                        }
                        if (vm.PersonalInfoData && vm.PersonalInfoData.length > 0) {
                            (vm.FormSchema)[4].Data[0].value = vm.PersonalInfoData[0].address;
                            (vm.FormSchema)[4].Data[1].value = vm.PersonalInfoData[0].city;
                            (vm.FormSchema)[5].Data[0].value = vm.PersonalInfoData[0].state;
                            (vm.FormSchema)[5].Data[1].value = vm.PersonalInfoData[0].country_id;
                            (vm.FormSchema)[6].Data[0].value = vm.PersonalInfoData[0].zipcode;
                        }
                        await vm.GetCountryName('', '', vm.PersonalInfoData[0].country_id);
                        if(vm.candidateData.length > 0){
                        var jsonobj = {
                            FIRSTNAME: vm.candidateData[0].FIRST_NAME,
                            MIDDLENAME: vm.candidateData[0].MIDDLE_NAME,
                            LASTNAME: vm.candidateData[0].LAST_NAME,
                            PERSONALEMAIL: vm.candidateData[0].email_id,
                            PHONE: vm.candidateData[0].phone_number,
                            MOBILENUMBER: vm.candidateData[0].phone_number,
                            dateofbirth: vm.candidateData[0].date_of_birth,
                            GENDER: 1,
                            ADDRESS: vm.PersonalInfoData.length==0?'':vm.PersonalInfoData[0].address,
                            CITY: vm.PersonalInfoData.length==0?'':vm.PersonalInfoData[0].city,
                            STATE: vm.PersonalInfoData.length==0?'':vm.PersonalInfoData[0].state,
                            ZIPCODE: vm.PersonalInfoData.length==0?'':vm.PersonalInfoData[0].zipcode,
                            COMPANYID: vm.CompanyId,
                            CREATEDBY: vm.Userid,
                            APPLICANTID: vm.ApplicantId,
                            POSTID: vm.JobId,
                            CountryName: vm.CountryName
                        };
                        vm.PersonalDetailObj.push(jsonobj);
                        vm.$parent.GetPersonalInformation(vm.PersonalDetailObj);
                    }
                    }
                });
        },
        GetCountryName: function (x, y, id) {
            var vm = this;
            if (x == "") {
                var filterEventName = {};
                filterEventName = vm.CountryData.filter(z => z.value == id);
                vm.CountryName = filterEventName[0].name;
            } else {
                var filterEventName = {};
                filterEventName = vm.CountryData.filter(z => z.value == y.value);
                vm.CountryName = filterEventName[0].name;
            }
        },
        ShowCountry: async function () {
            var vm = this;
            vm.CountryData = [];
            var params = `ids=&moduleName=&type=COUNTRIES&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[5].Data[1].config.options = response.data.DATA;
                    vm.CountryData = response.data.DATA;
                }
                vm.$refs.PersonalDetailForm.UpdateKeyValue();
            })
        },
        Close: function () {
            this.$parent.AddTagPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.PersonalDetailObj=[];
            var jsonobj = {
                FIRSTNAME: formfields.FirstName,
                MIDDLENAME: formfields.MiddleName,
                LASTNAME: formfields.LastName,
                PERSONALEMAIL: formfields.PersonalEmail,
                PHONE: formfields.Phone,
                MOBILENUMBER: formfields.MobileNumber,
                dateofbirth: formfields.DateOfBirth,
                GENDER: formfields.Gender,
                ADDRESS: formfields.Street,
                CITY: formfields.City,
                STATE: formfields.State,
                COUNTRY: formfields.Country,
                ZIPCODE: formfields.ZipCode,
                COMPANYID: vm.CompanyId,
                CREATEDBY: vm.Userid,
                APPLICANTID: vm.ApplicantId,
                POSTID: vm.JobId,
                CountryName: vm.CountryName
            };
            vm.PersonalDetailObj.push(jsonobj);
            vm.$parent.GetPersonalInformation(vm.PersonalDetailObj);
            var params = JSON.stringify(jsonobj);
            DataServices.SavePersonalInformation(params).then((response) => {
                if (response.data[0].Status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("EventLocationInfo")]), "success", true, vm.$t("Alert"));
                    vm.$parent.loadData("Resource");
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("EventLocationInfo")]), "failure", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>