<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-body">
                        <div class="col-lg-12 mb-3 my-2 float-left">
                            <div class="heading-border">
                                <h5 class="h5"><span> {{ $t('Preview') }}</span></h5>
                            </div>
                            <p class="description mt-2">
                                <span v-html="emailtemplate"></span>
                            </p>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 text-right">
                            <a href="javascript:;" v-on:click="close" class="btn btn-danger formbtn">
                                <i class="fa fa-times"></i> {{ $t('Cancel') }}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        TemplateId: {
            type: Number,
            required: true
        },
        PersonalDetailObj: {
            type: Object,
            required: true
        },
        CompensationDetailObj: {
            type: Object,
            required: true
        },
        EmploymentDetailObj: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            txtEmailSubject: '',
            emailtemplate: '',
            CompanyName: '',
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyName = this.GetUserInfo.CompanyName;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `templateId=${vm.TemplateId}`;
            await DataServices.GetEmailTemplate(url).then(
                function (response) {
                    if (response.data.DATA.length > 0) {
                        vm.emailtemplate = response.data.DATA[0].template_subject;
                        var email = response.data.DATA[0].template_content;
                        var emailSubject = response.data.DATA[0].template_subject;
                        var includeinOfferletter = vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.INCLUDEOFFERLETTER;
                        var stocksData = vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].STOCKBENEFITDATA[0];
                        //vm.jsonToTable(stocksData);
                        var StocksName = vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].STOCKBENEFITDATA[0];
                        var amount = vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.AMOUNT;
                        const numberFormatter = Intl.NumberFormat('en-US');
                        var result = email.replaceAll("@candidatefirstname@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].FIRSTNAME)
                            .replaceAll("@MiddleName@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].MIDDLENAME)
                            .replaceAll("@LastName@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].LASTNAME)
                            .replaceAll("@jobtitle@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].JOBTITLE)
                            .replaceAll("@companyname@", vm.CompanyName)
                            .replaceAll("@candidateemail@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].PERSONALEMAIL)
                            .replaceAll("@candidatedateofbirth@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].dateofbirth)
                            .replaceAll("@reportingmanager@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].ReportManagerName)
                            .replaceAll("@candidatedepartment@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].DepartmentName)
                            .replaceAll("@jobdept@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].DepartmentName)
                            .replaceAll("@candidateaddress@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].CountryName + ' ' + vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].STATE + ' ' + vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].ADDRESS + ' ' + vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].CITY + ' ' + vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].ZIPCODE)
                            .replaceAll("@candidatelocation@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].LOCATION.label)
                            .replaceAll("@candidateemploymenttype@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].EmployeeTypeName)
                            .replaceAll("@candidatejoiningdate@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].JOININGDATE)
                            .replaceAll("@candidatejobvalidity@", vm.EmploymentDetailObj.length == 0 ? '' : vm.EmploymentDetailObj[0].ENDDATE)
                            .replaceAll("@compensationsalary@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.CurrencyName + ' ' + amount)
                            .replaceAll("@Remuneration@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.RemunerationName)
                            .replaceAll("@Currency@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.CurrencyName)
                            .replaceAll("@compensationperiod@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.PeriodName)
                            .replaceAll("@PaidAs@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].COMPENSATIONDATA.PaidAsName)
                            .replaceAll("@compensationstocks@", includeinOfferletter != true ? '' : vm.CompensationDetailObj.length == 0 ? '' : vm.CompensationDetailObj[0].STOCKBENEFITQTY[0])
                            .replaceAll("@CompanyUrl@", '')
                            .replaceAll("@CompanyAddress@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].ADDRESS)
                            .replaceAll("@CompanyCity@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].CITY)
                            .replaceAll("@CompanyState@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].STATE)
                            .replaceAll("@CompanyZip@", vm.PersonalDetailObj.length == 0 ? '' : vm.PersonalDetailObj[0].ZIPCODE)

                        vm.emailtemplate = result;
                    }
                }
            );
        },
        jsonToTable: function (json) {
            document.getElementById("tableID").innerHTML = '';
            var headers = ["StoksName ", "StocksQuantity"];
            this.table = document.getElementById("tableID");
            for (var i = 0; i < json.length; i++) {
                var row = this.table.insertRow(i);
                row.insertCell(0).innerHTML = json[i].stock;
                row.insertCell(1).innerHTML = json[i].quantity;
            }
            var header = this.table.createTHead();
            var headerRow = header.insertRow(0);
            for (var i = 0; i < headers.length; i++) {
                headerRow.insertCell(i).innerHTML = headers[i];
            }
            document.getElementById("tableID").innerHTML = this.table.innerHTML;
        },
        close: function () {
            this.$parent.showpreviewModal = false;
        },
    }
}
</script>