<template>
    <section>
        <dynamic-form ref="CompensationForm" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            <template v-slot:tgslot-CompensationBenefitInformation="{ data }">
                <div class="row" v-for="(item, index) in StockData">
                    <div class="input-group form-check-inline">
                        <div class="col-sm-12 col-md-12 col-lg-5">
                            <div class="form-group">
                                <label>{{ $t("BenefitStockName") }} </label>
                                <input type="text" class="form-control" v-model="item.stock">
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-5">
                            <div class="form-group">
                                <label>{{ $t("QuantityInfo") }} </label>
                                <input type="number" class="form-control" v-model="item.quantity">
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-2 d-flex align-items-center">
                            <div class="form-group mb-0 mt-2">
                                <a v-if="index == 0" v-on:click="AddRow"
                                    class="theme-primary round-icon-small btn-success-light"><em
                                        class="fa fa-plus"></em></a>
                                <a v-if="index > 0" v-on:click="DeleteRow(item, index)" href="javascript:;"
                                    class="btn-danger-light theme-primary round-icon-small"><em
                                        class="fa fa-trash"></em></a>
                            </div>
                        </div>
                    </div>
                </div>
            </template>
        </dynamic-form>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CurrencyData: [],
            candidateData: [],
            CompensationDetailObj: [],
            CompanyId: null,
            Userid: null,
            RemunerationName: '',
            PeriodName: '',
            PaidAsName: '',
            StockData: [{ stock: "", quantity: "" }],
            newItem: '',
            StocksName: '',
            StockQty: '',
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: this.$t('CompensationInformation'),
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: this.$t('IncludeinOfferLetter'),
                            name: "OfferLetter",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Remuneration'),
                            name: "Remuneration",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "Salary" }, { value: "2", name: "Commission" }, { value: "3", name: "Fee" }],
                                onChange: this.GetDDLDataName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": false },
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Amount Payble'),
                            name: "AmountPayble",
                            value: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Currency'),
                            name: "Currency",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetDDLDataName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Period'),
                            name: "Period",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "Annually" }, { value: "2", name: "Monthly" }, { value: "3", name: "Quarterly" }, { value: "4", name: "Biweekly" }, { value: "5", name: "Weekly" }, { value: "6", name: "Daily" }],
                                onChange: this.GetDDLDataName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PaidAs'),
                            name: "PaidAs",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "Monthly" }, { value: "2", name: "Biweekly" }, { value: "3", name: "Weekly" }],
                                onChange: this.GetDDLDataName
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    groupId: null,
                    layoutType: "single",
                    group_name: this.$t('CompensationBenefitInformation'),
                    visibility: true,
                    Data: [
                        {
                            astype: "SlotField",
                            label: '',
                            name: "CompensationBenefitInformation",
                            value: "",
                            visibility: true,
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: "",
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Continue",
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        await vm.GetCurrency();
        this.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&postid=${vm.JobId}`;
            await DataServices.GetOfferCompensation(url).then(async function (response) {
                vm.candidateData = [];
                if (response.data != null && response.data != "") {
                    vm.candidateData = response.data.DATA;
                    (vm.FormSchema)[0].Data[0].value = vm.candidateData[0].include_offer_letter == true ? "true" : "";
                    (vm.FormSchema)[0].Data[1].value = vm.candidateData[0].remuneration_id;
                    (vm.FormSchema)[1].Data[0].value = vm.candidateData[0].amount;
                    (vm.FormSchema)[1].Data[1].value = vm.candidateData[0].currency_id;
                    (vm.FormSchema)[2].Data[0].value = vm.candidateData[0].period_id;
                    (vm.FormSchema)[2].Data[1].value = vm.candidateData[0].paid_as_id;
                    if (response.data.BENEFITS != null) {
                        vm.StockData = [];
                        response.data.BENEFITS.forEach(function (item, key) {
                            if (item.qty == 0) { item.qty = '' }
                            if (vm.StocksName != '') {
                                vm.StocksName = vm.StocksName.concat(", ").concat(item.Benefit);
                            }
                            else {
                                vm.StocksName = item.Benefit;
                                vm.StockQty = item.qty;
                            }
                            vm.StockData.push({ stock: item.Benefit, quantity: item.qty });
                        });
                    }
                    if (vm.candidateData[0].remuneration_id == 1) {
                        vm.RemunerationName = 'Salary';
                    } else if (vm.candidateData[0].remuneration_id == 2) {
                        vm.RemunerationName = 'Commission';
                    } else if (vm.candidateData[0].remuneration_id == 3) {
                        vm.RemunerationName = 'Fee';
                    }
                    await vm.GetDDLDataName('Currency', '', vm.candidateData[0].currency_id);
                    await vm.GetDDLDataName('Period', '', vm.candidateData[0].period_id);
                    await vm.GetDDLDataName('PaidAs', '', vm.candidateData[0].period_id);
                    var maincompensationdata = {
                        INCLUDEOFFERLETTER: vm.candidateData[0].include_offer_letter == "true" ? true : false,
                        REMUNERATION: vm.candidateData[0].remuneration_id,
                        AMOUNT: parseInt(vm.candidateData[0].amount),
                        COMPANYID: vm.CompanyId,
                        CREATEDBY: vm.Userid,
                        APPLICANTID: vm.ApplicantId,
                        POSTID: vm.JobId,
                        RemunerationName: vm.RemunerationName,
                        CurrencyName: vm.CurrencyName,
                        PeriodName: vm.PeriodName,
                        PaidAsName: vm.PaidAsName
                    }
                    var data = {
                        COMPENSATIONDATA: maincompensationdata,
                        STOCKBENEFITDATA: vm.StocksName,
                        STOCKBENEFITQTY: vm.StockQty
                    }
                    vm.CompensationDetailObj.push(data);
                    vm.$parent.GetCompensationInformation(vm.CompensationDetailObj);
                }
            })
        },
        GetCurrency: async function () {
            var vm = this;
            vm.CurrencyData = [];
            var params = `ids=&moduleName=&type=CURRENCY_FOR_EVENT&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[1].Data[1].config.options = response.data.DATA;
                    vm.CurrencyData = response.data.DATA;
                }
                vm.$refs.CompensationForm.UpdateKeyValue();
            })
        },
        GetDDLDataName: function (x, y, id) {
            var vm = this;
            if (y.name == "Remuneration") {
                vm.RemunerationName = '';
                if (y.value == '1') {
                    vm.RemunerationName = 'Salary';
                } else if (y.value == '2') {
                    vm.RemunerationName = 'Commission';
                } else if (y.value == '3') {
                    vm.RemunerationName = 'Fee';
                }
            }
            if (y.name == "Currency" || x == "Currency") {
                vm.CurrencyName = '';
                if (x == "Currency") {
                    var filterEventName = {};
                    filterEventName = vm.CurrencyData.filter(z => z.value == id);
                    vm.CurrencyName = filterEventName[0].name;
                } else {
                    var filterEventName = {};
                    filterEventName = vm.CurrencyData.filter(z => z.value == y.value);
                    vm.CurrencyName = filterEventName[0].name;
                }
            }
            if (y.name == "Period" || x == "Period") {
                vm.PeriodName = '';
                if (x == "Period") {
                    vm.PeriodName = '';
                    if (id == '1') {
                        vm.PeriodName = 'Annually';
                    } else if (id == '2') {
                        vm.PeriodName = 'Monthly';
                    } else if (id == '3') {
                        vm.PeriodName = 'Quarterly';
                    } else if (id == '4') {
                        vm.PeriodName = 'Biweekly';
                    } else if (id == '5') {
                        vm.PeriodName = 'Weekly';
                    } else if (id == '6') {
                        vm.PeriodName = 'Daily';
                    }
                } else {
                    vm.PeriodName = '';
                    if (y.value == '1') {
                        vm.PeriodName = 'Annually';
                    } else if (y.value == '2') {
                        vm.PeriodName = 'Monthly';
                    } else if (y.value == '3') {
                        vm.PeriodName = 'Quarterly';
                    } else if (y.value == '4') {
                        vm.PeriodName = 'Biweekly';
                    } else if (y.value == '5') {
                        vm.PeriodName = 'Weekly';
                    } else if (y.value == '6') {
                        vm.PeriodName = 'Daily';
                    }
                }
            }
            if (y.name == "PaidAs" || x == "PaidAs") {
                vm.PaidAsName = '';
                if (x == "PaidAs") {
                    vm.PaidAsName = '';
                    if (id == '1') {
                        vm.PaidAsName = 'Monthly';
                    } else if (id == '2') {
                        vm.PaidAsName = 'Biweekly';
                    } else if (id == '3') {
                        vm.PaidAsName = 'Quarterly';
                    }
                } else {
                    vm.PaidAsName = '';
                    if (y.value == '1') {
                        vm.PaidAsName = 'Monthly';
                    } else if (y.value == '2') {
                        vm.PaidAsName = 'Biweekly';
                    } else if (y.value == '3') {
                        vm.PaidAsName = 'Weekly';
                    }
                }
            }
        },
        AddRow: function () {
            var vm = this;
            vm.StockData.push({ stock: "", quantity: "" });
        },
        DeleteRow: function (item, index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Field")), function () {
                vm.StockData.splice(index, 1);
            });
        },
        Close: function () {
            this.$parent.AddTagPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.CompensationDetailObj=[];
            var totalquantity = 0;
            var totalstock = 0;
            var stockbenefitdata = [];
            var stockbenefitqty = [];
            vm.StockData.forEach(function (item, key) {
                if (Array.isArray(item.quantity)) {
                    totalquantity = item.quantity;
                }
                else {
                    totalquantity = item.quantity;
                }
                stockbenefitqty.push(totalquantity);
            });
            vm.StockData.forEach(function (item, key) {
                var groupIds = 0;
                if (Array.isArray(item.stock)) {
                    totalstock = item.stock;
                }
                else {
                    totalstock = item.stock;
                }
                stockbenefitdata.push(totalstock);
            });
            var maincompensationdata = {
                INCLUDEOFFERLETTER: formfields.OfferLetter == "true" ? true : false,
                REMUNERATION: formfields.Remuneration,
                AMOUNT: parseInt(formfields.AmountPayble),
                CURRENCY: formfields.Currency,
                PERIOD: formfields.Period,
                PAIDAS: formfields.PaidAs,
                COMPANYID: vm.CompanyId,
                CREATEDBY: vm.Userid,
                APPLICANTID: vm.ApplicantId,
                POSTID: vm.JobId,
                RemunerationName: vm.RemunerationName,
                CurrencyName: vm.CurrencyName,
                PeriodName: vm.PeriodName,
                PaidAsName: vm.PaidAsName
            }
            var data = {
                COMPENSATIONDATA: maincompensationdata,
                STOCKBENEFITDATA: stockbenefitdata,
                STOCKBENEFITQTY: stockbenefitqty
            }
            vm.CompensationDetailObj.push(data);
            vm.$parent.GetCompensationInformation(vm.CompensationDetailObj);
            var params = JSON.stringify(data);
            DataServices.SaveCompensationInformation(params).then((response) => {
                if (response.data[0].Status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("EventLocationInfo")]), "success", true, vm.$t("Alert"));
                    vm.$parent.loadData("Service");
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("EventLocationInfo")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>